@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&display=swap);
:root {
  --theme-color: white;
  --primary-color: #404040;
  --secundary-color: #f7c331;
  --text-color-inverse: white;
  --text-color: #404040;
  --navbar-shallow-color: #e5e5e5;
  --menu-mobile-background-color: #3a4460;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: white;
  background-color: var(--theme-color);
  color: #404040;
  color: var(--text-color);
  padding-top: 70px;
}

a {
  text-decoration: none;
  color: #404040;
  color: var(--text-color);
}

.profile-img {
  width: 85%;
  max-width: 300px;
  border-radius: 50%;
}

.profile-img-section {
  position: relative;
}

@media (min-width: 950px) {
  .profile-img {
    width: 100%;
    max-width: 410px;
    transform: scaleX(-1);
    border-radius: 50%;
  }
}

.profile-info {
  margin-bottom: 25px;
}

.profile-details {
  font-size: 17px;
  font-weight: 500;
  line-height: 1.3;
  max-width: 450px;
  margin: 20px auto;
}

.profile-title {
  font-size: 35px;
  font-weight: bold;
  margin: 0;
  color: var(--primary-color);
  font-family: "Noto Sans", sans-serif;
}

@media (min-width: 950px) {
  .profile-info {
    margin-bottom: 0px;
  }

  .profile-title {
    font-size: 60px;
  }

  .profile-details {
    font-size: 17px;
    margin: 30px 0;
  }
}

.horizontal-padding {
  padding-left: 25px;
  padding-right: 25px;
}
.vertical-padding {
  padding-top: 25px;
  padding-bottom: 25px;
}

@media (min-width: 950px) {
  .horizontal-padding {
    padding-left: 70px;
    padding-right: 70px;
  }
  .vertical-padding {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}

.profile {
  text-align: center;
}

@media (min-width: 950px) {
  .profile {
    text-align: left;
    display: flex;
    justify-content: space-between;
    max-width: 980px;
    margin: 0 auto;
    align-items: center;
  }
}

